import React from "react";
import {Expander, Section, Toggle} from "eshop/modules/core/components/ui/Expander";
import {connect} from "react-redux";
import {OraLoader} from "eshop/components/OraCommonComponents";
import {reservationData} from "eshop/modules/checkout/selectors";
import {printInvoiceNumber} from "eshop/modules/checkout/actions/app";

export class MulticartGoodsOrdersDocumentsView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            document: {title: "Faktura", description: "Wydrukuj fakturę"},
            sectionId: "expander-content-invoice",
            printInvoiceError: false
        }
    }

    handleClick(event) {
        event.preventDefault();
        this.props.printInvoiceNumber();
    }

    renderDocumentLink() {
        return  (<a href="#" onClick={this.handleClick.bind(this)}>{this.state.document.description}</a>);
    }

    render() {
        const toggleHeader = <h3 className="h4 opl-section__heading">{this.state.document.title}</h3>
        const sectionHeader = <div className="l-row"><Toggle variant="arrow" header={toggleHeader} className="l-col l-col-12 " triggerClassName="opl-document__section__trigger"><span className="opl-section__text-show">rozwiń sekcję Umowy</span><span className="opl-section__text-hide">zwiń sekcję Umowy</span></Toggle></div>

        return (
            <div hidden={!this.showInvoice()}>
                <Section header={sectionHeader}
                         id={"document-component-subsection-expander-content-invoice"}
                         variant="section"
                         className={"opl-document__section"}
                         contentClassName="opl-document__section__content">
                    <div className="u-padding-left-l-xl u-small-no-padding-l u-padding-m">
                        <ul>
                            <OraLoader loading={false} id={"print-documents-loader-invoice-1"}>
                                {this.renderDocumentLink()}
                            </OraLoader>
                        </ul>
                    </div>
                </Section>
            </div>
        );
    }

    showInvoice() {
        return this.props.reservation.paymentAndFiscalization
            && !!this.props.reservation.paymentAndFiscalization.isInvoicePresent
            && !!this.props.reservation.paymentStatus;
    }
}

const mapStateToProps = state => ({
    reservation: reservationData(state)
});

const mapDispatchToProps = dispatch => ({
    printInvoiceNumber: () => dispatch(printInvoiceNumber()),
});

const MulticartGoodsOrdersDocuments = connect(mapStateToProps, mapDispatchToProps)(MulticartGoodsOrdersDocumentsView);
export default MulticartGoodsOrdersDocuments;
